.login-page {
  background: url('@{app-image-directory}/background-image.jpg') no-repeat;
  background-attachment: fixed;
  background-color : #0D3B6C;
}

.login-panel {
  position: absolute;
  top: 150px;
  left: 50%;
  width: @login-panel-width;
  background-color: #fff;
  .opacity(0.90);
  margin-left: unit((unit(@login-panel-width)/-2),px);
  border-radius: @border-radius;
  > .login-panel-header {
    padding: 5px 0px;
    > h3 {
      text-align: center;
      margin: 0px 0px;
      padding: 5px 0px 10px 0px;
      border-bottom: 1px dotted #eee;
      font-weight: 700;
    }
  }

  > .login-panel-body {
    padding: 20px 80px;
  }

  > .login-panel-footer {
    padding: 5px;
    > a:after {
      content: "\00a0\007c\00a0";
    }
    > a:last-child:after {
      content: "\00a0\00a0\00a0";
    }
  }
}
