.mmGrid {
  margin-top: 3px;
  .mmg-headWrapper {
    border-top : 0px;
    .mmg-head th .mmg-titleWrapper .mmg-title {
      display: inline-block;
      *display: inline;
      *zoom: 1;
      height: 1.5em;
      padding-top: 8px;
      font-weight: 700;
      font-size: 12px;
      line-height: 1.5;
      vertical-align: top;
    }
  }
  .mmg-bodyWrapper {
    .mmg-body td {
      font-weight: 400;
      font-size: 12px;
      height: 2;
      line-height: 2;
      vertical-align: top;
    }
  }
}

.mmPaginator .limit{
  display: none;
}
