.main-header {
  position: relative;
  max-height: 100px;

  .navbar {
    border-width: 0px;
    margin-bottom: 0px;
    border-radius: 0;
    min-height: @main-header-height;
    height: @main-header-height;

    .nav > .active >a {
      background-color: #00a65a;
      color: #fff;
    }
  }

  >.logo {
    display: block;
    float: left;
    width: @main-header-logo-width;
    min-height: @main-header-height;
    line-height: 1.42857143;
  }


}


